<script setup>
import SearchFlight from "@/components/SearchFlight";
import FlightDetailInfo from "@/components/FlightDetailInfo";
//import JetType from './JetType.vue'
//import SelectedJet from './SelectedJet.vue'

import emitter from "tiny-emitter/instance";
import { ref, inject } from "vue";
import axios from "axios";

const $loading = inject("$loading");

const base_url = "https://test.api.impjets.com/v1/";
let step = ref("initial");
let distance = ref("Not Found.");

let tripType = ref("one-way");
let dataArray = ref([]);

let allDetails = {
  name: "",
  email: "",
  phone: "",
  msg: "",
  aircraft: "",
  origin: "",
  dest: "",
  date: "",
  pax: "",
  at: "",
  dt: "",
  callmeat: "",
  marketing_materials: "No",
  origin_details: {},
  destination_details: {},
};
let emptyVal = allDetails;
let data_copy = ref(allDetails);

emitter.on("exchangeInput", function () {
  let temp = allDetails.origin;
  allDetails.origin = allDetails.dest;
  allDetails.dest = origin;

  let temp_d = allDetails.origin_details;
  allDetails.origin_details = allDetails.destination_details;
  allDetails.destination_details = temp_d;
});

emitter.on("updateSearch", function (value) {
  if (value.path) {
    step.value = value.path;
  }

  if (value.from == "search") {
    step.value = "initial";

    allDetails.origin = value.data.origin_details.code_ata;
    allDetails.dest = value.data.destination_details.code_ata;
    allDetails.pax = value.data.pax;
    allDetails.origin_details = value.data.origin_details;
    allDetails.destination_details = value.data.destination_details;
    allDetails.date = value.data.date;

    calcCrow(
      allDetails.origin_details.coords_lat,
      allDetails.origin_details.coords_long,
      allDetails.destination_details.coords_lat,
      allDetails.destination_details.coords_long
    );

    setTimeout(function () {
      step.value = value.path;
    }, 400);
  } else if (value.from === "flight-detail") {
    allDetails.aircraft = value.data.aircraft_type;
    allDetails.dt = value.data.departing_time;
    allDetails.at = value.data.arriving_time;
    allDetails.name = value.data.first_name + " " + value.data.last_name;
    allDetails.email = value.data.email;
    allDetails.phone = value.data.phone;
    allDetails.msg = value.data.message;
    let loader = $loading.show({});
    axios
      .post(base_url + "ext.charter/request", allDetails, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        (response) => {
          emitter.emit("requestIsDone");
          allDetails = emptyVal;
          emitter.emit("reset");
          loader.hide();
        },
        (error) => {
          console.error(error);
          loader.hide();
        }
      );
  }

  data_copy.value = allDetails;
});

function calcCrow(lat1, lon1, lat2, lon2) {
  let $pointA = { lat: lat1, lng: lon1 };
  let $pointB = { lat: lat2, lng: lon2 };

  let d = impapi_distance($pointA, $pointB);
  // console.log(d, 'New  Distnce')
  distance.value = d.toFixed(2);
}

function calc_distance(lat1, lat2, lng1, lng2) {
  const R = 6371e3; // metres
  const φ1 = (lat1 * Math.PI) / 180; // φ, λ in radians
  const φ2 = (lat2 * Math.PI) / 180;
  const Δφ = ((lat1 - lat2) * Math.PI) / 180;
  const Δλ = ((lng2 - lng1) * Math.PI) / 180;
  const a =
    Math.sin(Δφ / 2) * Math.sin(Δφ / 2) +
    Math.cos(φ1) * Math.cos(φ2) * Math.sin(Δλ / 2) * Math.sin(Δλ / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return c * R * 0.000621371;
}

function impapi_distance($pointA, $pointB) {
  const $R = 6371e3; // metres
  let M_PI = Math.PI;
  let $x1 = ($pointA["lat"] * M_PI) / 180; // φ, λ in radians
  let $x2 = ($pointB["lat"] * M_PI) / 180;
  let $dx = (($pointA["lat"] - $pointB["lat"]) * M_PI) / 180;
  let $dy = (($pointB["lng"] - $pointA["lng"]) * M_PI) / 180;
  let $a =
    Math.sin($dx / 2) * Math.sin($dx / 2) +
    Math.cos($x1) * Math.cos($x2) * Math.sin($dy / 2) * Math.sin($dy / 2);
  let $c = 2 * Math.atan2(Math.sqrt($a), Math.sqrt(1 - $a));
  return $c * $R * 0.000621371 * 0.868976;
}

function chageTripType(type) {
  tripType.value = type;
  step.value = "initial";
  // allDetails = emptyVal;
  // emitter.emit('updateButtonType', {value :'next'});
  emitter.emit("dateRest");
}

// Converts numeric degrees to radians
function toRad(Value) {
  return (Value * Math.PI) / 180;
}
</script>

<template>
  <section class="section-search">
    <div class="container">
      <div class="search-panel" id="search-panel">
        <div class="search-panel__type">
          <button
            type="button"
            :class="{
              'btn-primary': tripType === 'one-way',
              'btn-dark-outline': tripType !== 'one-way',
            }"
            class="btn btn-sm"
            @click="chageTripType('one-way')"
          >
            One way
          </button>
          <button
            type="button"
            :class="{
              'btn-primary': tripType === 'round-trip',
              'btn-dark-outline': tripType !== 'round-trip',
            }"
            class="btn btn-sm"
            @click="chageTripType('round-trip')"
          >
            Round trip
          </button>
        </div>
        <!-- Round Trip -->
        <SearchFlight
          :tripType="tripType"
        />
        <FlightDetailInfo v-if="step == 'flight-detail'" />
      </div>
    </div>
  </section>
</template>
