import FooterLayout from "@/layouts/FooterLayout.vue";
import Search from "@/components/Search.vue";
import FeedbackSection from "@/components/sections/FeedbackSection.vue";
import SupportSection from "@/components/sections/SupportSection.vue";
import { useRoute } from "vue-router";
import Axios from "axios";
import { useCommonStore } from "@/stores/common";

const commonStore = useCommonStore();

const route = useRoute();

const article = ref();
const articles = ref([]);

const articleBySlug = computed(() => {
  Object.keys(articles.value).filter((key) => {
    console.log(articles.value[key])
    console.log(route.params.id)
    if (articles.value[key].url == route.params.id) {
      article.value = articles.value[key];
    }
  });
  return article.value;
});

function getArticles() {
  Axios({
    method: "get",
    url: `${process.env.BASE_URL}data/imp.charter.articles.json`,
  }).then(
    (res) => {
      console.log(res.data[0].articles);
      articles.value = res.data[0].articles;
    },
    (err) => {
      console.log(err);
    }
  );
}

onMounted(() => {
  {
    getArticles();
  }
});
</script>
