<template>
  <div class="search-panel__flight-detail">
    <div class="search-field select-unit">
      <select name="" id="field-aircraft" required="" v-model="aircraft_type">
        <option value="0" disabled>Select preferred aircraft type/size</option>
        <option :value="index+1" v-for="(aircraft, index) in aircraftsArray" :key="index" >{{aircraft}}</option>
      </select>
      <label class="field-label" for="field-aircraft">
        Aircraft Type
      </label>
    </div>
    <div class="search-field select-unit">
      <select name="" id="field-departing-time" required="" v-model="departing_time" >
        <option value="" disabled="" selected="" hidden=""></option>
        <option :value="index" v-for="index in timesArray" :key="index" >{{index}}</option>
      </select>
      <label class="field-label" for="field-departing-time">
        Departing time
      </label>
    </div>
    <div class="search-field select-unit">
      <select name="" id="field-arriving-time" required="" v-model="arriving_time" >
        <option value="" disabled="" selected="" hidden=""></option>
        <option :value="index" v-for="index in timesArray" :key="index" >{{index}}</option>
      </select>
      <label class="field-label" for="field-arriving-time">
        {{  tripType === 'round-trip' ? 'Return Departure Time' :  'Arrival Time' }}
      </label>
    </div>
  </div>
  <div class="search-panel__personal-detail-caption">
    Personal Information
  </div>
  <div class="search-panel__personal-detail">
    <div class="search-field">
      <input class="field-input" id="field-first-name" v-model="first_name" autocomplete="off" type="text" placeholder=" ">
      <label class="field-label" for="field-first-name">
        First name
      </label>
    </div>
    <div class="search-field">
      <input class="field-input" id="field-last-name" v-model="last_name" autocomplete="off" type="text" placeholder=" ">
      <label class="field-label" for="field-last-name">
        Last name
      </label>
    </div>
    <div class="search-field">
      <input class="field-input" id="field-email" v-model="email" autocomplete="off" type="email" placeholder=" ">
      <label class="field-label" for="field-email">
        Email
      </label>
    </div>
    <div class="search-field">
      <input class="field-input" id="field-phone" v-model="phone" autocomplete="off" type="tel" placeholder=" ">
      <label class="field-label" for="field-phone">
        Phone number
      </label>
    </div>
  </div>
  <div class="search-panel__message">
    <div class="search-field">
      <input class="field-input" id="field-message" v-model="message" autocomplete="off" type="text" placeholder=" ">
      <label class="field-label" for="field-message">
        Message
      </label>
    </div>
    <div class="search-button">
      <button class="btn btn-primary" type="button" @click="submitForm">Submit</button>
    </div>
  </div>
</template>

<script>
import emitter from 'tiny-emitter/instance'

export default {
  name: "FlightDetailInfo",

  props: {
    tripType: String,
  },

  data() {
    return {
      timesArray: [],
      aircraftsArray:  ['Turbo', 'Light','Heavy'],
      aircraft_type: '0',
      departing_time: '',
      arriving_time: '',
      first_name: '',
      last_name: '',
      email: '',
      phone: '',
      message: ''
    }
  },

  methods: {
    initTimeArray() {
      let times = [],
          tt = 0,
          x = 60,
          ap = [' AM', ' PM'];

      for (let i = 0; tt < 24*60; i++) {
        let hh = Math.floor(tt / 60);
        let mm = (tt % 60);
        let hour = hh % 12;
        times[i] = ("0" + ((hour == 0 ? 12 : hour))).slice(-2) + ':' + ("0" + mm).slice(-2) + ap[Math.floor(hh/12)];
        tt = tt + x;
      }

      return times;
    },

    submitForm() {
      emitter.emit('updateSearch', {
        data: {
          aircraft_type: this.aircraft_type,
          departing_time: this.departing_time,
          arriving_time: this.arriving_time,
          first_name: this.first_name,
          last_name: this.last_name,
          email: this.email,
          phone: this.phone,
          message: this.message
        },
        path: 'initial',
        from : 'flight-detail'
      });
    }
  },

  created() {
    this.timesArray = this.initTimeArray();
  }


}
</script>
