<template>
  <div class="search-panel__main">
    <div class="search-field">
      <SearchPlaceinput type="origin" :defaultData="defaultOrigin" />
      <label class="field-label" for="origin">
        <i class="icon-flight-up"></i>
        Origin
      </label>
    </div>
    <div :class="{ swapped: toggleIcon }" class="field-swap">
      <button @click="exchangeOriDest()">
        <i class="icon-arrow-left-right"></i>
      </button>
    </div>
    <div class="search-field">
      <SearchPlaceinput type="destination" :defaultData="defaultDest" />
      <label class="field-label" for="destination">
        <i class="icon-flight-down"></i>
        Destination
      </label>
    </div>

    <DatePicker
      v-if="props.tripType !== 'one-way'"
      is-range
      class="search-field single-input"
      v-model="range"
      :columns="1"
      :min-date="new Date()"
      @dayclick="onDayClick(event)"
      :is-required="true"
      ref="calendar"
    >
      <template #default="{ range, togglePopover, updateValue }">
        <input
          class="field-input"
          @click="togglePopover()"
          id="datepicker"
          readonly
          :value="range"
        />
        <label class="field-label-datepicker" for="datepicker">
          <i class="icon-calendar"></i>
          Departure - Return
        </label>
      </template>
    </DatePicker>

    <DatePicker
      v-if="props.tripType === 'one-way'"
      class="search-field single-input"
      v-model="date"
      :columns="1"
      :min-date="new Date()"
      @dayclick="onDayClick(event)"
      :is-required="true"
      ref="calendar"
    >
      <template #default="{ date, togglePopover }">
        <input
          class="field-input"
          @click="togglePopover()"
          id="datepicker"
          readonly
          :value="date"
        />
        <label class="field-label-datepicker" for="datepicker">
          <i class="icon-calendar"></i>
          Departure
        </label>
      </template>
    </DatePicker>

    <div
      class="search-field"
      @click="travelerFocus = true"
    >
      <div class="dropdown-block">
        <button
          class="btn-dropdown"
          @focusout.self="handleFocusOut"
          @click="togglePassangerCountFun()"
        >
          <template v-if="travelerFocus">
            {{ passangerCount }}
            {{ passangerCount > 1 ? "Travelers" : "Traveler" }}
          </template>
        </button>
        <div
          v-bind:class="['dropdown', { active: togglePassangerCount }]"
          class=""
        >
          <div class="counter">
            <button
              class="btn btn-counter"
              @focusout.self="handleFocusOut"
              @click.stop.prevent="changeCount($event, 'decrement')"
            >
              -
            </button>
            <div class="count">{{ passangerCount }}</div>
            <button
              class="btn btn-counter"
              @focusout.self="handleFocusOut"
              @click.stop.prevent="changeCount($event, 'increment')"
            >
              +
            </button>
          </div>
        </div>
      </div>
      <label
        class="field-label-traveler"
        :class="{ focused: travelerFocus }"
        for="travelers"
      >
      <i class="icon-travelers"></i>Travelers
      </label>
    </div>
    <div class="search-button" v-if="props.tripType != 'multi-trip'">
      <button
        class="btn btn-primary"
        @click="search()"
        :class="{ change: !toggleCustomerInfo }"
      >
        Get A Quote <i class="arrow-left-circle-white"></i>
      </button>
    </div>
  </div>
</template>

<script setup>
import axios from "axios";
import SearchPlaceinput from "./SearchPlaceInput.vue";
import emitter from "tiny-emitter/instance";
import { DatePicker } from "v-calendar";
import "v-calendar/dist/style.css";
import { ref, reactive } from "vue";

const props = defineProps({
  tripType: String,
});
const picked = ref(new Date());
let allTrip = ref([1]);

const togglePassangerCount = ref(false);
const passangerCount = ref(1);
const date = ref(new Date());
let lastDay = date.value;
const defaultData = ref([]);
const toggleCustomerInfo = ref(false);
const calendar = ref(null);

const travelerFocus = ref(false);

let defaultDest = ref("");
let defaultOrigin = ref("");
let toggleIcon = ref(false);

let incredecre = ref(false);
let range = ref({
  start: new Date(),
  end: new Date(),
});

function addTrip() {
  let arr = allTrip.value;
  let last = arr[arr.length - 1];
  arr.push(++last);
}

function removeTrip(index) {
  let arr = allTrip.value;
  arr.splice(index, 1);
}

let searchRequest = {
  origin: "",
  dest: "",
  date: picked.value,
  pax: passangerCount.value,
  origin_details: {},
  destination_details: {},
};
let copySearchRequest = {
  origin: "",
  dest: "",
  date: picked.value,
  pax: passangerCount.value,
  origin_details: {},
  destination_details: {},
};

function forceRender1() {}
let timeoutVar;
function togglePassangerCountFun() {
  if (clearTimeout) clearTimeout(timeoutVar);
  togglePassangerCount.value = !togglePassangerCount.value;
  timeoutVar = setTimeout(function () {
    togglePassangerCount.value = false;
  }, 1500);
}

emitter.on("dateRest", function () {
  setTimeout(function () {
    if (props.tripType === "one-way") {
      document.getElementById("datepicker").placeholder = dateToYMD(new Date());
      date.value = dateToYMD(new Date());
    } else {
      range.value = { start: new Date(), end: new Date() };
      // range.value.end = new Date();
      document.getElementById("datepicker").placeholder =
        dateToYMD(new Date()) + " - " + dateToYMD(new Date());
      date.value = dateToYMD(new Date()) + " - " + dateToYMD(new Date());
    }
  }, 400);
  toggleCustomerInfo.value = true;
});

emitter.on("reset", function () {
  date.value = new Date();
  searchRequest = {
    origin: "",
    dest: "",
    date: picked.value,
    pax: passangerCount.value,
    origin_details: {},
    destination_details: {},
  };
  passangerCount.value = 1;
  toggleCustomerInfo.value = true;
});

function dateToYMD(date) {
  var d = date.getDate();
  var m = date.getMonth() + 1; //Month from 0 to 11
  var y = date.getFullYear();
  return "" + (m <= 9 ? "0" + m : m) + "/" + (d <= 9 ? "0" + d : d) + "/" + y;
}

function onDayClick(e) {
  if (props.tripType === "one-way") {
    // if(!date.value)

    emitter.emit("focuInput", { type: "passengerCount" });
    document.getElementById("datepicker").placeholder = dateToYMD(date.value);
    date.value = dateToYMD(date.value);
  } else {
    document.getElementById("datepicker").placeholder =
      dateToYMD(range.value.start) + " - " + dateToYMD(range.value.end);
    date.value =
      dateToYMD(range.value.start) + " - " + dateToYMD(range.value.end);
  }
}
//

emitter.on("focuInput", function (value) {
  if (value.type == "dateTimePicker") {
    setTimeout(function () {
      document.getElementById("datepicker").click();
    }, 200);
    // this.togglePopover();
    //  calendar.move(5)
  } else if (value.type == "passengerCount") {
    togglePassangerCount.value = true;

    timeoutVar = setTimeout(function () {
      togglePassangerCount.value = false;
    }, 1500);
  }
});

function exchangeOriDest() {
  toggleIcon.value = !toggleIcon.value;
  let org = searchRequest.origin;
  searchRequest.origin = searchRequest.dest;
  searchRequest.dest = org;
  emitter.emit("exchangeInputField", {
    type: "origin",
    value: searchRequest.origin,
  });
  emitter.emit("exchangeInputField", {
    type: "destination",
    value: searchRequest.dest,
  });
  emitter.emit("exchangeInput");
}

emitter.on("updateInput", function (type, value) {
  if (type === "origin") {
    // this.$refs.origin.$el.focus()
    searchRequest.origin = value.name;
    searchRequest.origin_details = value.details;
  } else {
    searchRequest.dest = value.name;
    searchRequest.destination_details = value.details;
  }
});

emitter.on("updateButtonType", function (data) {
  toggleCustomerInfo.value = data.value;
});

function changeCount(event, type) {
  if (clearTimeout) clearTimeout(timeoutVar);
  incredecre.value = true;
  if (type == "decrement") {
    if (passangerCount.value > 1)
      passangerCount.value = passangerCount.value - 1;
  } else {
    passangerCount.value = passangerCount.value + 1;
  }

  timeoutVar = setTimeout(function () {
    togglePassangerCount.value = false;
  }, 1500);
  setTimeout(function () {
    incredecre.value = false;
  }, 300);
}

function search() {
  if (searchRequest.origin == "") {
    emitter.emit("focuInput", { type: "origin" });
  } else if (searchRequest.dest == "") {
    emitter.emit("focuInput", { type: "destination" });
  } else {
    searchRequest.pax = passangerCount.value;
    searchRequest.date = date.value;
    togglePassangerCount.value = false;
    toggleCustomerInfo.value = false;
    emitter.emit("updateSearch", {
      data: searchRequest,
      path: "flight-detail",
      from: "search",
    });
  }
}

function handleFocusOut() {
  setTimeout(function () {
    if (!incredecre.value) {
      togglePassangerCount.value = false;
    }
  }, 200);
}
</script>
